// Core Stuff
import React from "react"
import { Link, graphql } from "gatsby"
import Img from "gatsby-image"
// Components
import SEO from "../components/seo"
import Layout from "../components/layout"
import Sidebar from "../components/sidebar"
// Utility
import kebabCase from "lodash/kebabCase"

const IndexPage = ({ data }) => (
  <Layout>
    <SEO title="Home" />
    <div className="col-md-4" id="content-desktop">
      <Sidebar />
    </div>
    <div className="col-md-8">
      <br />
      <div className="alert alert-secondary" role="alert">
        <h4 className="my-1 text-secondary">
          أحدث المواضيع <i className="fas fa-blog"></i>
        </h4>
      </div>
      {data.allMarkdownRemark.edges.map(post => (
        <div key={post.node.id} className="card mb-4">
          <Link to={post.node.fields.slug}>
            <Img
              className="card-img-top"
              fluid={post.node.frontmatter.image.childImageSharp.fluid}
            />
          </Link>
          <div className="card-body">
            <Link to={post.node.fields.slug} className="text-dark nounderline">
              <h3 dir="auto" className="card-title">
                {/* add padge say "new" - you can use if statement to let it be in
              only the latest post*/}
                <i className="fas fa-book"></i> {post.node.frontmatter.title}
              </h3>
            </Link>
            <p dir="rtl" className="card-text text-secondary">
              {post.node.excerpt}
              <Link
                to={post.node.fields.slug}
                className="btn btn-secondary btn-sm"
              >
                <i className="far fa-paper-plane"></i> أقرأ المزيـد
              </Link>
            </p>

            <ul>
              {post.node.frontmatter.tags.map(tag => (
                <Link key={tag} to={`/tags/${kebabCase(tag)}`}>
                  {" "}
                  <span className="badge badge-warning">{tag}</span>
                </Link>
              ))}{" "}
              : <i className="fa fa-tags fa-sm"></i> الوســوم
              <br />
              تم نشره في <i className="far fa-calendar-alt"></i> :{" "}
              {post.node.frontmatter.date}
            </ul>
          </div>
        </div>
      ))}
    </div>
  </Layout>
)
export const pageQuery = graphql`
  query BlogIndexQuery {
    allMarkdownRemark(sort: { fields: frontmatter___date, order: DESC }) {
      edges {
        node {
          id
          fields {
            slug
          }
          excerpt(pruneLength: 200, truncate: true, format: PLAIN)

          frontmatter {
            title
            date
            author
            tags
            image {
              childImageSharp {
                fluid(maxWidth: 750, maxHeight: 300) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`

export default IndexPage
